import { IonSpinner } from '@ionic/react'
import { observer } from 'mobx-react-lite'
import React, { useState } from 'react'
import { RouteComponentProps } from 'react-router'

import { Page } from '../../components'
import { plata_fmt } from '../../helpers'
import { useStores } from '../../models/root-store/root-store-context'
import { OrdenVenta } from '../../services/api'

const moment = require('moment-timezone')

const QRCode = require('qrcode.react')

export const AdminImprimir: React.FC<RouteComponentProps> = observer(({ history }) => {
  const rootStore = useStores()
  const [Item, setItem] = useState<OrdenVenta>()
  const [Loading, setLoading] = useState(true)

  const setup = () => {
    const idOrden = history.location.pathname.split('/')[3]
    obtenerOrden(parseInt(idOrden))
  }

  React.useEffect(() => {
    setup()
  }, [])

  const obtenerOrden = (idOrden: number) => {
    rootStore.environment.api.adminGetOrdenVenta(idOrden).then((result) => {
      if (result.kind === 'ok') {
        setItem(result.orden_venta)
      }
    })
    setTimeout(() => setLoading(false), 500)
  }

  return (
    <Page login_required header={<div></div>} footer={<div></div>}>
      {Loading && (
        <div className="page-wrapper" style={{ display: 'flex', height: '100vh', overflowY: 'scroll' }}>
          <div className="container-fluid background-white">
            <div className={'d-flex justify-content-center loader'}>
              <IonSpinner name="crescent" className={'loadingSpinner'} />
            </div>
          </div>
        </div>
      )}
      {!Loading && Item && (
        <div>
          <div className={'row'}>
            <div className={'col-8 mx-auto'}>
              <div className={'table-responsive'} style={{ overflowX: 'hidden' }}>
                <table className={'table table-bordered  my-5'}>
                  <tr>
                    <td colSpan={6} className={'text-center'} style={{ backgroundColor: '#b5cbdb' }}>
                      <strong>ORDEN DE VENTA</strong>
                    </td>
                  </tr>
                  <tr>
                    <td>
                      <strong>Referencia</strong>
                    </td>
                    <td>{Item.referencia}</td>
                    <td colSpan={2}>
                      <strong>Productos</strong>
                    </td>
                    <td colSpan={2}>{Item.pedido.productos.length}</td>
                  </tr>
                  <tr>
                    <td>
                      <strong>Fecha de retiro</strong>
                    </td>
                    <td>{moment(Item.fecha_retiro).format('DD/MM/YYYY')}</td>
                    <td colSpan={2}>
                      <strong>Hora de retiro</strong>
                    </td>
                    <td colSpan={2}>{moment(Item.fecha_retiro).format('HH:mm')}</td>
                  </tr>
                  {Item.transportista && (
                    <tr>
                      <td colSpan={6} className={'text-center'} style={{ backgroundColor: '#b5cbdb' }}>
                        <strong>TRANSPORTISTA</strong>
                      </td>
                    </tr>
                  )}
                  {Item.transportista && (
                    <tr>
                      <td>
                        <strong>Nombre</strong>
                      </td>
                      <td>{Item.transportista.usuario.first_name}</td>
                      <td>
                        <strong>Apellido</strong>
                      </td>
                      <td>{Item.transportista.usuario.last_name}</td>
                      <td>
                        <strong>Documento</strong>
                      </td>
                      <td>{Item.transportista.usuario.nro_documento}</td>
                    </tr>
                  )}
                  {Item.vehiculo && (
                    <tr>
                      <td>
                        <strong>Marca</strong>
                      </td>
                      <td>{Item.vehiculo.marca}</td>
                      <td>
                        <strong>Modelo</strong>
                      </td>
                      <td>
                        {Item.vehiculo.modelo} {Item.vehiculo.ano}
                      </td>
                      <td>
                        <strong>Número de chapa</strong>
                      </td>
                      <td>{Item.vehiculo.numero_chapa}</td>
                    </tr>
                  )}
                  <tr>
                    <td colSpan={6} className={'text-center'} style={{ backgroundColor: '#b5cbdb' }}>
                      <strong>DETALLES</strong>
                    </td>
                  </tr>
                  <tr>
                    <td colSpan={6} className={'m-0 p-0 '}>
                      <hr style={{ border: 1, borderStyle: 'dashed' }} />
                    </td>
                  </tr>
                  <tr>
                    <td colSpan={6} className={'m-0 p-0'}>
                      <div className={'row'}>
                        <div className={'col-10 pr-0'}>
                          <div className={'table-responsive'}>
                            <table className={'table'}>
                              <thead>
                                <tr>
                                  <th colSpan={3}>
                                    <strong>Pedido #{Item.pedido.referencia}</strong>
                                  </th>
                                </tr>
                                <tr>
                                  <th>Producto</th>
                                  <th>Cantidad</th>
                                  <th>Costo</th>
                                </tr>
                              </thead>
                              <tbody>
                                {Item.pedido.productos.map((prod) => {
                                  return (
                                    <tr>
                                      <td>{prod.nombre}</td>
                                      <td>{prod.cantidad}</td>
                                      <td>{plata_fmt(prod.costo)}</td>
                                    </tr>
                                  )
                                })}
                              </tbody>
                            </table>
                          </div>
                        </div>
                        <div className={'col-2 pl-0'}>
                          {Item.contrasena && (
                            <div className={'text-center'}>
                              <QRCode value={Item.contrasena} size={128} className={'mx-auto pt-2'} />
                              <h1 className={'pt-2'} style={{ color: 'black' }}>
                                <strong>{Item.contrasena}</strong>
                              </h1>
                            </div>
                          )}
                        </div>
                      </div>
                    </td>
                  </tr>
                </table>
                <hr style={{ border: 1, borderStyle: 'dashed' }} />
              </div>
            </div>
          </div>
        </div>
      )}
    </Page>
  )
})

export default AdminImprimir
